  content: url(../image/svg/l6-how-timeline.svg);
  top: 20px;
  left: 50%;
  z-index: -1;
  display: none;
}
@media (min-width: 500px) {
  .gr-timeline-wrapper .single-timeline-feature:before {
    width: 95%;
    overflow: hidden;
    display: block;
  }
}
@media (min-width: 768px) {
  .gr-timeline-wrapper .single-timeline-feature:before {
    width: 95%;
    overflow: hidden;
  }
}
@media (min-width: 1200px) {
  .gr-timeline-wrapper .single-timeline-feature:before {
    width: 100%;
    overflow: unset;
  }
}

